import React, {useState} from "react";
import {Link} from "react-router-dom";

function LoginForm() {
    const [username, setUsername] = useState("");
    const [password, setPassword] = useState("");
    const [confirmPassword, setConfirmPassword] = useState("");
    const [error, setError] = useState("");

    const handleSubmit = async (e) => {
        e.preventDefault();

        if (!username || !password || !confirmPassword) {
            setError("   .");
            return;
        }

        if (password !== confirmPassword) {
            setError("  .");
            return;
        }

        try {
            const response = await fetch("http://localhost:8080/join", {
                method: "POST",
                headers: {
                    "Content-Type": "application/json",
                },
                body: JSON.stringify({username, password}),
            });

            if (response.ok) {
                console.log("ok");
                const result = await response.text(); //  "ok"   
                console.log(" :", result);
                setError(""); //     
                alert(' .')
                window.location.href = '/';
            } else {
                setError(" :   .");
            }
        } catch (error) {
            setError(" :   .");
        }
    };

    return (
        <>
            <Link to='/' className='mapBack'></Link>

            <form onSubmit={handleSubmit}>
                <div className='loginForm'>
                    <h1></h1>

                    <div className='loginForm_page'>
                        <div className='loginForm_input'>
                            <input type="text" id='id' onChange={(e) => setUsername(e.target.value)}/>
                            <label htmlFor="id"></label>
                        </div>

                        <div className='loginForm_input'>
                            <input type="password" id='password' onChange={(e) => setPassword(e.target.value)}/>
                            <label htmlFor="password"></label>
                        </div>

                        <div className='loginForm_input' style={{marginBottom: '10px'}}>
                            <input type="password" id='confirmPassword' onChange={(e) => setConfirmPassword(e.target.value)}/>
                            <label htmlFor="confirmPassword"> </label>
                        </div>

                        <Link to='/login' className='signUpAndLogin'></Link>

                        <button type='submit' className='submitBtn'></button>
                    </div>
                </div>
            </form>
        </>
    );
}

export default LoginForm;