import React, {useEffect, useRef, useState} from "react";
import areaData from "./data/areaData";
import './App.css'
import {backRequest} from "./BackRequest";
import {Collapse} from '@kunukn/react-collapse'
import Map from "./Map";
import ResultLi from "./ResultLi";
import {Link, Route, Routes} from "react-router-dom";
import HospitalInfo from "./HospitalInfo";
import LoginForm from "./login/LoginForm";
import SignupForm from "./join/SignupForm";

function App() {

    //                // setCityArea()             // default
    // / 
    const [cityArea, setCityArea] = useState('/');

    // // 
    const [districtArea, setDistrictArea] = useState('//');

    // // 
    const [addressDetailArea, setAddressDetailArea] = useState('//');

    //   20      
    const [searchCount, setSearchCount] = useState('0');

    //   &  
    const [hospitalData, setHospitalData] = useState([]);

    //     ul  ref
    const listRef = useRef(null);

    //           1 
    const scrollCount = useRef(1);

    //  toggle
    const [toggle, setToggle] = useState(false);

    //  &      
    const [markerData, setMarkerData] = useState([]);

    //          
    const [userLocation, setUserLocation] = useState({});

    //     
    const [research, setResearch] = useState(false)

    //  
    const [word, setWord] = useState('');

    //       
    const [wordSearchNo, setWordSearchNo] = useState(false);

    //   scroll 
    const [wordSearchFrag, setWordSearchFrag] = useState(false);

    const token = localStorage.getItem('accessToken');

    useEffect(() => {
        console.log(token)
    }, [token]);

    //    
    // define the function that finds the users geolocation
    const getUserLocation = () => {
        // if geolocation is supported by the users browser
        if (navigator.geolocation) {
            // get the current users location
            navigator.geolocation.getCurrentPosition(
                (position) => {
                    // save the geolocation coordinates in two variables
                    const { latitude, longitude } = position.coords;
                    // update the value of userlocation variable
                    setUserLocation({lat : latitude, lng : longitude});
                },

                // if there was an error getting the users location
                (error) => {
                    console.error('Error getting user location:', error);
                }
            );
        }

        // if geolocation is not supported by the users browser
        else {
            console.error('Geolocation is not supported by this browser.');
        }
    };

    //        
    useEffect(() => {
        //     
        getUserLocation();
    }, []);
    ///////////////////////////////////////////////////////////////////////////////

    // /   
    const cityClick = (city) => {
        console.log(city.a)
        if (city.a === undefined)
            setCityArea('/');
        else
            setCityArea(city.a);

        setDistrictArea('//');
        setAddressDetailArea('//');
    }

    // //   
    const districtClick = (district) => {
        console.log(district.a)
        if (district.a === undefined)
            setDistrictArea('//');
        else
            setDistrictArea(district.a);
        setAddressDetailArea('//');
    }

    // //   
    const addressDetailClick = (detail) => {
        console.log(detail.a);
        if (detail.a === undefined)
            setAddressDetailArea('//');
        else
            setAddressDetailArea(detail.a);
    }

    //       ( <=> )
    let [fade, setFade] = useState('');

    const searchResultClick = () => {
        if (fade === 'hide')
            setFade('');
        else
            setFade('hide')
    }

    //     slideToggle 
    //   
    const resetSearch = () => {
        console.log('')
        setCityArea('/');
        setDistrictArea('//');
        setAddressDetailArea('//');
        setToggle(!toggle)
    }

    const addressSearch = () => {
        if (cityArea === '/')
            alert(' /  .');
        else {
            if (scrollCount > 1) {
                scrollCount.current = 1;
            }

            setToggle(!toggle)

            setWordSearchFrag(false)

            backRequest('/api/search/cityDistrictDetail', {
                cityCode: cityArea,
                districtCode: districtArea,
                addressDetailCode: addressDetailArea,
                page: scrollCount.current
            }).then((response) => {
                setHospitalData(response.dataList);

                setSearchCount(response.totalCount.toLocaleString('ko-KR'));

                setUserLocation([response.dataList[3].x_coordinate, response.dataList[3].y_coordinate])

                if(hospitalData.length === 20){
                    setResearch(false)
                }else
                    setResearch(true)

                unLimitScrollMarkerAdd(response.dataList, true);
            })
        }

        console.log(cityArea, districtArea, addressDetailArea)
    }


    const wordSearch = (e) => {
        e.preventDefault();

        if(e.target.word.value === '')
            alert(' .')
        else {
            if (scrollCount.current > 1)
                scrollCount.current = 1;

            setWordSearchFrag(true)

            backRequest('/api/search/wordSearch', {
                word : e.target.word.value,
                page : scrollCount.current
            }).then((response) => {
                console.log(response)
                //    
                if (response.dataList.length === 0) {
                    console.log(' ')
                    setWordSearchNo(true)
                } else {
                    console.log(' ')

                    setWord(e.target.word.value);

                    setWordSearchNo(false)

                    setHospitalData(response.dataList);

                    setSearchCount(response.totalCount.toLocaleString('ko-KR'));

                    setUserLocation([response.dataList[0].x_coordinate, response.dataList[0].y_coordinate])

                    setResearch(true)

                    unLimitScrollMarkerAdd(response.dataList, true);
                }
            });
        }
    }

    const unLimitScroll = () => {
        const element = listRef.current;

        if (element) {
            //    
            if (element.scrollHeight - element.scrollTop === element.clientHeight) {
                scrollCount.current = scrollCount.current + 1

                if(wordSearchFrag){
                    backRequest('/api/search/wordSearch', {
                        word : word,
                        page: scrollCount.current
                    }).then((response) => {
                        setHospitalData([...hospitalData, ...response.dataList]);

                        setResearch(false)

                        unLimitScrollMarkerAdd(response.dataList, false);
                    })

                }else {
                    backRequest('/api/search/cityDistrictDetail', {
                        cityCode: cityArea,
                        districtCode: districtArea,
                        addressDetailCode: addressDetailArea,
                        page: scrollCount.current
                    }).then((response) => {
                        setHospitalData([...hospitalData, ...response.dataList]);

                        setResearch(true)

                        unLimitScrollMarkerAdd(response.dataList, false);
                    })
                }
            }
        }
    }

    const unLimitScrollMarkerAdd = (data, research) => {
            //                   // a = 
        let sample = data.map(a => ({
            "code": [a.x_coordinate, a.y_coordinate],
            "data" : a //  
        }));

        if(research){
            setMarkerData(sample);
        }else {
            //   =>  
            setMarkerData([...markerData, ...sample]);
        }
    }

    return (
        <>
            <Routes>
                <Route path='/' element={
                    <>
                        <Map userLocation={userLocation} markerData={markerData} research={research}/>

                        <div className='searchWrap' >
                            <div className='searchMain' onClick={() => setToggle(!toggle)}>
                                <span id='cityCode'>{cityArea}</span>
                                <span id='districtCode'>{districtArea}</span>
                                <span id='addressDetailCode'>{addressDetailArea}</span>
                            </div>

                            <Collapse
                                isOpen={toggle}
                                transition="height 500ms"
                            >
                                <div className='searchDetailWrap'>
                                    <div style={{width: '25%'}}>
                                        <ul tabIndex='0'>
                                            <li style={{textAlign: 'center'}}><strong
                                                onClick={() => cityClick({a: undefined})}>/</strong></li>
                                            {                             //     //index
                                                Object.keys(areaData).map((a, i) =>
                                                    <li><span style={{textAlign: 'center'}}
                                                              onClick={() => cityClick({a})}>{a}</span></li>
                                                )
                                            }
                                        </ul>
                                    </div>
                                    <div style={{width: '40%', borderLeft: '1px solid #eee', marginBottom: '10px'}}>
                                        <ul tabIndex='0'>
                                            <li><strong onClick={() => districtClick({a: undefined})}>//</strong></li>
                                            {
                                                cityArea === '/' ? '' :
                                                    Object.keys(areaData[cityArea]).map((a, i) =>
                                                        <li><span onClick={() => districtClick({a})}>{a}</span></li>
                                                    )
                                            }
                                        </ul>
                                    </div>
                                    <div style={{width: '35%', borderLeft: '1px solid #eee', marginBottom: '10px'}}>
                                        <ul tabIndex='0'>
                                            <li><strong onClick={() => addressDetailClick({a: undefined})}>//</strong></li>
                                            {
                                                districtArea === '//' ? '' :
                                                    Object.values(areaData[cityArea][districtArea]).map((a, i) =>
                                                        <li><span onClick={() => addressDetailClick({a})}>{a}</span></li>
                                                    )
                                            }
                                        </ul>
                                    </div>
                                    <p className='searchBtn'>
                                        <span onClick={() => addressSearch()}></span>
                                        <span onClick={() => resetSearch()}></span>
                                    </p>
                                </div>
                            </Collapse>
                        </div>
                        <div className={"searchResultWrap " + fade}>
                            <div className='searchText'>
                                <form onSubmit={wordSearch} name='wordSearch'>
                                    <input type="text" placeholder='   ' name='word'/>

                                    <button type='submit'><i className="fa-solid fa-magnifying-glass" style={{color: "#fff", fontSize: '14px'}}></i></button>
                                </form>
                            </div>

                            <div className='searchResultCenter'>
                                 ( <span>{searchCount}</span> )
                                <p>      .</p>
                            </div>

                            <div className='searchResultSuccess'>
                                <ul onScroll={() => unLimitScroll()} ref={listRef}>
                                    {
                                        wordSearchNo ?
                                            <div className='noData'>
                                                 .
                                            </div>
                                            : hospitalData.map((a, i) =>
                                                <ResultLi hospitalData={a} key={i} />
                                            )
                                    }
                                </ul>
                            </div>

                            <div className='mapFoot'>
                                <Link to='/login'></Link>
                                <Link to='/join'></Link>
                            </div>
                        </div>

                        <span className={fade === 'hide' ? "searchOpen" : "searchClose"} onClick={() => searchResultClick()}></span>
                    </>
                }/>
                <Route path="/hospitalInfo/:careCode" element={<HospitalInfo />} />

                <Route path="/login" element={<LoginForm />} />

                <Route path="/join" element={<SignupForm />} />
            </Routes>
        </>
    )
}

export default App;