import React from "react";

function ResultLi(props){

    const data = props.hospitalData;

    return (
        <li>
            <span className='hospitalTitle' onClick={() => window.open(`/hospitalInfo/${data.careCode}`, '_blank', 'width=1100, height=900')}>{data.hospitalName}</span>

            <p className='hospitalSubInfo'>
                <span>{data.hospitalType}</span>
                <span className='bar'></span>
                <span>{data.phoneNumber}</span>

                <span><i className='icon-home'></i>{data.address}</span>
            </p>
        </li>
    )
}

export default ResultLi;